'use strict';

import $ from './jquery-shim.js';

/**
 * ããã©ã«ããªãã·ã§ã³
 * @type {{}}
 */
var defaultOptions = {
  selector: '.js-anchor',
  dataSelector: 'anchor-target',
  scrollSpeed: 300,
  easing: 'linear'
};
export default class Anchor {
  constructor(options) {
    // ãªãã·ã§ã³ãã»ãã
    this.options = $.extend(defaultOptions, options);
    this.init();
  }


  /**
   * åæå
   * @param e ã¿ã¼ã²ããã¨ãªãã¨ã¬ã¡ã³ã
   */
  init() {
    this.target = $(this.options.selector);
    this.onClick();
  }

  /**
   * ã¯ãªãã¯æã®ã¤ãã³ã
   */
  onClick() {

    var self = this;

    this.target.on('click', function(e) {
      e.preventDefault();

      // ã¹ã¯ã­ã¼ã«åã®ã¿ã¼ã²ãããæå®
      var anchorTargetSelector = $(this).data(self.options.dataSelector);


      if (typeof anchorTargetSelector === 'undefined') {
        var href = $(this).attr('href');
        var anchorTargetSelector = href.match(/#(\S*)/g);
        if (typeof anchorTargetSelector[0] === 'undefined') {
          throw new Error('ã¿ã¼ã²ããã¨ãªãè¦ç´ ãåå¾ã§ãã¾ããã§ããã');
          return false;
        }
        anchorTargetSelector = anchorTargetSelector[0];
      }

      var anchorTarget = $(anchorTargetSelector);

      if (anchorTarget.length === 0) {
        throw new Error('ã¿ã¼ã²ããã¨ãªãè¦ç´ ãåå¾ã§ãã¾ããã§ããã');
        return false;
      }
      var headerH = 94;
      if (window.innerWidth <= 750) {
        headerH = 70;
      }
      var top = anchorTarget.offset().top - headerH;

      // ã¹ã¯ã­ã¼ã«ããã
      $('body,html').animate({
        scrollTop: top
      }, {
        duration: self.options.scrollSpeed,
        easing: self.options.easing
      });
    });
  }
}


