import  $ from "./jquery-shim.js"

var defaultOptions = {
    selector: '.js-fixedheader', // å®è¡ããã»ã¬ã¯ã¿
    offset: 500, // å®è¡ãããªãã»ãããã¯ã»ã«æ°,
    cssClass: ".l-global-navigation", // CSSãå®ç¾©ããã¦ããã¯ã©ã¹
    activeClass: "is-fixed", // æå¹ãªæã«ä»ä¸ããã¯ã©ã¹
    mobile: false // ã¢ãã¤ã«æã«ã©ãåä½ããã
}

export default class Fixedheader {

    constructor(options){
        this.options = $.extend(defaultOptions, options);
        this.init();
    }

    /**
     * åæåãã
     */
    init() {
        this.target = $(this.options.selector);

        this.isFixed = this.isFixed.bind(this);
        this.run = this.run.bind(this);
        if (
            ( screen.width > 768 && this.options.mobile )
            || this.options.mobile === false
        ) {
            window.requestAnimationFrame(this.run);
        }
    }


    /**
     * å®è¡
     */
    run() {
        if (this.isFixed()) {
            this.target.addClass(this.options.activeClass);
        } else {
            this.target.removeClass(this.options.activeClass);
        }

        window.requestAnimationFrame(this.run);
    }

    /**
     * å¤æ­ãã
     * @returns {boolean}
     */
    isFixed() {
        // ãªãã»ããããé«ããå¤æ­ãã
        return ( window.pageYOffset > this.options.offset ) ? true : false;
    }

}
