import $ from "./jquery-shim";


let defaultOptions = {
  selector: ".js-current-year",
}

export default class CopyRight {

  constructor(options) {
    this.options = $.extend(defaultOptions, options);
    this.init();
  }
  /**
   * åæå
   */
  init() {
    this.targetEle = $(this.options.selector);
    this.run();
  }

  /**
   * å®è¡
   */
  run() {
    this.targetEle.text(new Date().getFullYear());
  }
}
